    margin-top: 10px;
    position: absolute;
    bottom: 5px;
    left: 0;
    right: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.commonPro .downShare .share {
      font-size: 17px !important;
      padding-left: 10px;
      margin-top: 3px;
}
.commonPro .downShare .cart {
      font-size: 22px !important;
      position: relative;
      top: -1px;
      right: 5px;
}
.shareBar {
  position: relative;
  padding: 40px 15px 12px;
}
.shareBar ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-line-pack: start;
        align-content: flex-start;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    border: 0;
}
.shareBar ul li {
      -webkit-box-flex: 0;
          -ms-flex: 0 0 23%;
              flex: 0 0 23%;
      margin-bottom: 20px;
      text-align: center;
      padding-left: 0;
      border: 0;
}
.shareBar ul li img {
        width: 70%;
        display: inline-block;
}
.shareBar ul li .name {
        color: #666666;
        font-size: 15px;
}
.el-date-table td.current:not(.disabled) span {
  background-color: #e33946;
}
.el-date-table td.today span {
  color: #e33946;
}
#app {
  height: 100%;
  width: 100%;
}
.go-top {
  position: fixed;
  text-align: center;
  right: 10px;
  bottom: 30%;
  z-index: 1001;
  background-color: rgba(255, 255, 255, 0.95);
  border: solid 1px #dbdbdb;
  color: #959595;
  width: 30px;
  height: 30px;
  line-height: 30px;
  border-radius: 100%;
}
.pageLoading {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 1001;
  background-color: rgba(255, 255, 255, 0.3);
}
.pageLoading:after {
    content: '';
    background: url("common/images/loading.gif") no-repeat;
    background-size: 100% 100%;
    width: 200px;
    height: 150px;
    position: fixed;
    top: 50%;
    left: 50%;
    margin-left: -100px;
    margin-top: -75px;
    z-index: 1002;
}



